import Vue from 'vue'
import App from './App'
import {
  router
} from './router'
import axios from 'axios'
import store from './store' //store
import '@/assets/css/global.css' /**/
import '@/assets/iconfont/iconfont2.css' /**/
import '@/assets/js/rem.js' /*rem*/
import vueJsonp from "vue-jsonp"//jsop
Vue.use(vueJsonp )
import filter from '@/assets/js/filter' //
// 
import vueCanvasPoster from '@/assets/lib/index'
// 
Vue.use(vueCanvasPoster)
import {
  Toast
} from 'vant';
Vue.use(Toast);
import animate from 'animate.css'
Vue.use(animate)
// vue-amap
import AMap from 'vue-amap'
Vue.use(AMap)
Vue.config.productionTip = false
// 
Vue.prototype.$http = axios
// axios.defaults.baseURL = "/ebapi"
// 
axios.defaults.timeout = 10000;
// post
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8';
// get
axios.defaults.headers.get['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8';
// 
axios.interceptors.request.use(
  config => {
    // tokenhttpheadertoken
    // tokentoken
    let token = localStorage.getItem('token');
    token && (config.headers.token = token);
    return config;
  },
  error => {
    return Promise.error(error);
  })
//

axios.interceptors.response.use(
  response => {
    if (response.status === 200) {
      return Promise.resolve(response);
    } else {
      return Promise.reject(response);
    }
  },
  // 200
  error => {
    if (error.response.status) {
      switch (error.response.status) {
        case 304:
          Toast({
            message: '',
            duration: 1000,
            forbidClick: true
          });
          break;
          // 401: 
          // 
          // 
        case 100:
          router.replace({
            path: '/login',
            query: {
              redirect: router.currentRoute.fullPath
            }
          });
          break;
          // 403 token
          // 
          // tokenvuextoken
          // 
        case 403:
          Toast({
            message: '',
            duration: 1000,
            forbidClick: true
          });
          // token
          localStorage.removeItem('token');
          // store.commit('loginSuccess', null);
          // fullPath
          setTimeout(() => {
            router.replace({
              path: '/login',
              query: {
                redirect: router.currentRoute.fullPath
              }
            });
          }, 1000);
          break;
          // 404
        case 404:
          Toast({
            message: '',
            duration: 1500,
            forbidClick: true
          });
          break;
          // 
        default:
          Toast({
            message: error.response.data.message,
            duration: 1500,
            forbidClick: true
          });
      }
      return Promise.reject(error.response);
    }
  }
);
// vue-amap
AMap.initAMapApiLoader({
  // key
  key: '9cc1cb3ed4c140f6945d80dec47528f4',
  //  
  plugin: ['AMap.Autocomplete', 'AMap.PlaceSearch', 'AMap.Scale', 'AMap.OverView', 'AMap.ToolBar', 'AMap.MapType',
    'AMap.PolyEditor', 'AMap.CircleEditor'
  ]
});
//
Vue.prototype.getLoing = function(isLoing, callBack) {
  console.log('')
  isLoing && callback && callback();
}
new Vue({
  el: '#app',
  router,
  store, //store
  components: {
    App
  },
  template: '<App/>'
})



