import Vue from 'vue'
export default (function() {
  Vue.filter('status', function(value) {
    if (value == 1) {
      return ''
    } else if (value == 0) {
      return ''
    } else {
      return ''
    }
  })
  Vue.filter('statusOrder', function(value) {
    if (value == 1) {
      return ''
    } else if (value == 2) {
      return ''
    } else if (value == 3) {
      return ''
    } else {
      return ''
    }
  })
  Vue.filter('gender', function(value) {
    if (value == 1) {
      return ''
    } else if (value == 2) {
      return ''
    } else {
      return ''
    }
  })
  Vue.filter('card_level', function(value) {
    if (value == 0) {
      return ''
    } else if (value == 1) {
      return ''
    } else {
      return ''
    }
  })
  Vue.filter('from', function(value) {
    if (value == 'exchange') {
      return ''
    } else if (value == 'register') {
      return ''
    } else if (value == 'sys') {
      return ''
    } else if (value == 'birthday') {
      return ''
    } else {
      return ''
    }
  })
  Vue.filter('state', function(value) {
    if (value == 'using') {
      return ''
    } else if (value == 'used') {
      return ''
    } else if (value == 'nouse') {
      return ''
    }
  })
  Vue.filter('order_status', function(value) {
    if (value == '1') {
      return ''
    } else if (value == '0') {
      return ''
    } else if (value == '-1') {
      return ''
    }
  })
  Vue.filter('rules', function(value) {
    return value
  })
  Vue.filter('fileLink', function(value) {
    const link = window.imgUrl + value
    return link
  })
  Vue.filter('toolType', function(value) {
    let type = ''
    if (value == 1) {
      type = ''
    } else if (value == 2) {
      type = ''
    }
    return type
  })
  Vue.filter('numToString', function(value) {
    const string = value.toString()
    return string
  })
  Vue.filter('projectState', function(value) {
    let string = ''
    switch (value) {
      case '1':
        string = ''
        break
      case '2':
        string = ''
        break
      case '3':
        string = ''
        break
    }
    return string
  })
  Vue.filter('time', function(value) {
    let day = moment.unix(value)
    let date = moment(day).format('YYYY/MM/DD H:mm')
    return date
  })
  Vue.filter('date', function(value) {
    let day = moment.unix(value)
    let date = moment(day).format('YYYY/MM/DD')
    return date
  })
  Vue.filter('abstract', function(value) {
    let abstract = ''
    if (value.length > 70) {
      abstract = value.substr(0, 70) + '...'
    } else {
      abstract = value
    }
    return abstract
  })
  Vue.filter('posStatus', function(value) {
    let status = ''
    switch (value) {
      case 1:
        status = ''
        break
      case 2:
        status = ''
        break
      case 3:
        status = ''
        break
    }
    return status
  })
  Vue.filter('template', function(value) {
    let template = ''
    if (value == '') {
      template = ''
    } else {
      template = ''
    }
    return template
  })


})()



